	module.exports = {"data":{"nimblRenderedHtml":{"html":"<h1 id=\"il-boom-economico\">Il boom economico</h1>\n<p>Gli Stati Uniti uscirono dalla guerra come <strong>prima potenza economica mondiale</strong>.</p>\n<p>Nelle case americane compaiono beni di consumo (frigorifero, radio, forno a gas, automobile,...) che in Europa arrivarono negli anni 50/60.<br>\nLa gente, comprando a rate, si indebitava.<br>\n<mark>La crescita economica degli Stati Uniti era fortissima</mark>, ciò rendeva facile ottenere presiti dalle banche.<br>\nIl Presidente Hoover affermò che la povertà sarebbe scomparsa dal Paese.</p>\n<p>Ci fu il <strong>boom speculativo</strong> in borsa: le azioni nelle borse (i titoli azionari) erano sempre in crescita e quindi anche i privati entravano nel &quot;gioco&quot; della borsa, <mark>sembrava che tutti potessero arricchirsi</mark>.</p>\n<h1 id=\"lo-scoppio-della-bolla\">Lo scoppio della bolla</h1>\n<p>Per soddisfare la domanda sempre crescente le industrie assumevano più operai e producevano di più finché non si arrivò alla <strong>crisi di sovrapproduzione</strong>.</p>\n<p>La sovrapproduzione fece si che <mark>gli azionisti rivendettero in massa le loro azioni</mark> facendo crollare il valore di queste ultime.<br>\nMolti azionisti rimasero sul lastrico e molte banche dichiararono banca rotta.</p>\n<p>Oltre a una crisi speculativa, ne seguì una economica, ci fu un <strong>credit crunch</strong>, ossia le banche non prestavano più soldi. Di conseguenza le aziende fallivano e si creava disoccupazione.<br>\n<mark>Gli USA smettono di acquistare beni dall’estero</mark>, in particolare dall’Europa. Anche il prezzo dei beni agricoli crolla.<br>\nUna delle nazioni europee che più ne risentì fu la Germania.</p>\n<h1 id=\"roosevelt-e-il-new-deal\">Roosevelt e il New Deal</h1>\n<p>Il Presidente Hoover di fronte a questa crisi venne sconfitto nelle elezioni del 32 da <strong>Roosevelt</strong>.</p>\n<p>Roosevelt presentò al Paese il <strong>New Deal</strong> che prevedeva:</p>\n<ul>\n<li><mark>sostenere la domanda interna finanziandola con la spesa pubblica</mark>, ossia lo Stato si indebita per finanziare opere pubbliche e assume un gran numero di disoccupati (3 milioni)</li>\n<li>controllare banche e industrie che normalmente avevano carta bianca. Negli stati Uniti dove lo Stato lasciava fare tutto quello che volevano alle aziende, era una grande riforma. La banca federale controllava le altre banche e vennero imposti salari minimi</li>\n<li>per risolvere la crisi agricola vennero distrutte derrate alimentari.</li>\n</ul>\n<p>La crisi dura fino al 1932.</p>\n","frontmatter":{"title":"I Roaring Twenties","pdfCompletoPath":null,"draft":false},"headings":[{"value":"<p>Il boom economico</p>\n","depth":1,"rawText":"Il boom economico\r"},{"value":"<p>Lo scoppio della bolla</p>\n","depth":1,"rawText":"Lo scoppio della bolla\r"},{"value":"<p>Roosevelt e il New Deal</p>\n","depth":1,"rawText":"Roosevelt e il New Deal\r"}]}},"pathContext":{"category":"Storia","title":"I Roaring Twenties"}}

